  <h1>RenamerFX</h1>
  <p>Cool GUI renamer application that looks funny. Had to make one because I convert my music to 96kbps opus when putting it on phone, but Android sucks at reading the tags correctly unless file-ending is ogg, which foobar2000 doesn't allow me to set if converting to opus. Instead of googling and writing the same PowerShell spell every time, making an app to do that makes perfect sense. Especially since it provided an splendid opportunity to finally learn JavaFX and Maven in a real non-trivial practical work.</p>
  <p>Its operation is simple: it replaces string in file name, overwriting the original file and does it without second-guessing did you really mean to rename this to that there. And it does it recursively.</p>
  <p>What's really cool about RenamerFX is that I made it also to be usable from command-line, providing an assisted interactive renaming or you can give it command-line args. It also ships as a completely stand-alone self-contained executable uber-jar. Only JRE required to run.</p>

