import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Footer from './components/Footer.vue'

Vue.config.productionTip = false

// global methods
Vue.mixin({
  methods: {
    locationIsHere: function(lastWord) {
      let arr = window.location.href.split('/')
      return arr[arr.length-1] === lastWord
    }
  }
})

// register Footer globally, other components can use without registering now
Vue.component('Footer', Footer)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
