import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import About from '@/views/About.vue'
import Writings from '@/views/Writings.vue'
import Computers from '@/views/writings/Computers.vue'
import WhyUbuntu from '@/views/writings/WhyUbuntu.vue'
import Editors from '@/views/writings/Editors.vue'
import Gtk from '@/views/writings/Gtk.vue'
import Kde4 from '@/views/writings/Kde4.vue'
import Kiinankieli from '@/views/writings/Kiinankieli.vue'
import Lisp1 from '@/views/writings/Lisp1.vue'
import Lisp2 from '@/views/writings/Lisp2.vue'
import UbuntuAdventure from '@/views/writings/UbuntuAdventure.vue'
import UbuntuTheBroken from '@/views/writings/UbuntuTheBroken.vue'
import Projects from '@/views/Projects.vue'
import Capstone from '@/views/projects/Capstone.vue'
import Gnome from '@/views/projects/Gnome.vue'
import Homepage from '@/views/projects/Homepage.vue'
import Kde from '@/views/projects/Kde.vue'
import Linux from '@/views/projects/Linux.vue'
import RenamerFX from '@/views/projects/RenamerFX.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', component: Home },
  { path: '/about', component: About },
  { path: '/writings', component: Writings, children: [
      { name: 'computers', path: 'computers', component: Computers },
      { name: 'whyubuntu', path: 'whyubuntu', component: WhyUbuntu },
      { name: 'editors', path: 'editors', component: Editors },
      { name: 'gtk', path: 'gtk', component: Gtk },
      { name: 'kde4', path: 'kde4', component: Kde4 },
      { name: 'kiinankieli', path: 'kiinankieli', component: Kiinankieli },
      { name: 'lisp1', path: 'lisp1', component: Lisp1 },
      { name: 'lisp2', path: 'lisp2', component: Lisp2 },
      { name: 'ubuntuadventure', path: 'ubuntuadventure', component: UbuntuAdventure },
      { name: 'ubuntuthebroken', path: 'ubuntuthebroken', component: UbuntuTheBroken }
    ]
  },
  { path: '/projects', component: Projects, children: [
      { name: 'capstone', path: 'capstone', component: Capstone },
      { name: 'gnome', path: 'gnome', component: Gnome },
      { name: 'homepage', path: 'homepage', component: Homepage },
      { name: 'kde', path: 'kde', component: Kde, },
      { name: 'linux', path: 'linux',component: Linux, },
      { name: 'renamerfx', path: 'renamerfx', component: RenamerFX }
    ],
  }
]

// possible full signature scrollBehavior(to, from, savedPosition)
const scrollBehavior = function() {
  // coordinates where 0,0 is top left
  // sets ever navigation action to scroll to top
  return { x: 0, y: 0 }
}

const router = new VueRouter({
  scrollBehavior,
  routes
})

export default router
