de extensive use of its components, only writing CSS myself when it was faster than reading how it's done with Bulma, or when Bulma's defaults needed overriding, which is thankfully easy to do without poisoning the project with !important mines.")]),_c('footer',[_vm._v("2020-11-29")]),_c('h2',[_vm._v("My old website")]),_c('img',{attrs:{"src":require("../../assets/html5-and-css3.png")}}),_vm._m(0),_c('p',[_vm._v("Neocities is very limited in its capabilities, so modern full-stack web development with a fancy SPA is a practical impossibility. That said, constrained development is quite rewarding. Trying to make this look and behave like it's modern without any JavaScript is an exploration.")]),_c('footer',[_vm._v("2017-04-11")]),_c('h2',[_vm._v("Capstone Project")]),_c('p',[_vm._v("Definitely classifies as web development: mobile-first MEAN full-stack and delivered MVP. "),_c('router-link',{attrs:{"to":{name:'capstone'}}},[_vm._v("Learn more from its dedicated page")]),_vm._v(".")],1)])}
